<script>
export default {
    name: "consulta_content",
    data() {
        return {
            transition_class: "SlideInXRight",
            contents: [
                "consulta_1.svg",
                "consulta_2.svg",
                "consulta_3.svg",
                "consulta_4.svg",
                "consulta_5.svg",
                "consulta_6.svg",
                "consulta_7.svg"
            ],
            content_index: 0
        }
    },
    methods: {
        right_callback() {
            this.$parent.$refs.consulta_content.scrollTop = 0
            this.$root.$el.scrollTop = 0
            this.content_index += 1
        },

        left_callback() {
            this.$parent.$refs.consulta_content.scrollTop = 0
            this.$root.$el.scrollTop = 0
            this.content_index -= 1
        }
    },
    computed: {
        current_content() {
            return this.contents[this.content_index]
        }
    },
    watch: {
        content_index: function(newVal, oldVal) {
            if (newVal > oldVal) {
                this.transition_class = "SlideInXRight"
            } else {
                this.transition_class = "SlideInXLeft"
            }
        }
    },
    beforeRouteLeave: function(to, from, next) {
        this.$parent.$refs.consulta_content.scrollTop = 0
        this.$root.$el.scrollTop = 0
        next()
    }
}
</script>

